import { Card } from '@consta/uikit/Card';
import { Text } from '@consta/uikit/Text';
import styles from './style.module.css';
import mion from './mion_logo.png';
import { DataTable } from './DataTable/DataTable';
import { File } from '@consta/uikit/File';
import { Attachment } from '@consta/uikit/Attachment';

export const ChatRow = ({
  speaker,
  text,
  timestamp,
  table,
  fileUrl,
  extension
}) => {
  function dataTable() {
    if (table !== null) {
      return <DataTable data={table} />;
    }
  }

  function getFilename() {
    return fileUrl.substring(fileUrl.lastIndexOf('/') + 1);
  }

  function file() {
    if (fileUrl !== null) {
      return (
        <Attachment
          fileName={getFilename()}
          fileExtension={extension}
          size="m"
          as="a"
          href={fileUrl}
          download
        />
      );
      // return <File extension={extension} style={{ marginTop: 10 }} />;
    }
  }

  if (speaker === 'user')
    return (
      <Card verticalSpace="s" horizontalSpace="5xl" className={styles.userRow}>
        <div className={styles.userFlex}>
          {/* <div className={styles.userAvatar}>            
        </div> */}
          <div className={styles.userText}>
            <Text size="l" className={styles.text}>
              {text}
            </Text>
            <div className={styles.timestamp}>
              <Text size="xs" className={styles.timestampText}>
                {timestamp}
              </Text>
            </div>
          </div>
        </div>
      </Card>
    );
  else if (speaker === 'mion')
    return (
      <Card verticalSpace="s" horizontalSpace="5xl" className={styles.mionRow}>
        <div className={styles.mionFlex}>
          {/* <div className={styles.mionAvatar}>
          <img src={mion} />
        </div> */}
          <div className={styles.mionText}>
            <div className={styles.tableContainer}>
              <Text
                size="l"
                className={styles.text}
                dangerouslySetInnerHTML={{ __html: text }}
              >
                {/* {text} */}
              </Text>
              {dataTable()}
              {file()}
            </div>
            <div className={styles.timestamp}>
              <Text size="xs" className={styles.timestampText}>
                {timestamp}
              </Text>
            </div>
          </div>
        </div>
      </Card>
    );
};
