import styles from './styles.module.css';
import { Text } from '@consta/uikit/Text';
import axios from 'axios';
import { TextField } from '@consta/uikit//TextField';
import { Button } from '@consta/uikit/Button';
import { IconSendMessage } from '@consta/uikit/IconSendMessage';
import React, { useReducer, useState, useRef, useEffect } from 'react';
import { ChatRow } from './ChatRow/ChatRow';
import { IconMoon } from '@consta/icons/IconMoon';
import { ThemeToggler } from '@consta/uikit/ThemeToggler';

let nextId = 0;
var height = 0;
const userId = (Math.random() + 1).toString(36).substring(7);

export const Chat = ({ innerRef }) => {
  // const apiUrl = 'http://146.185.234.11:8000/chat/';
  const apiUrl = 'http://146.185.234.11/back/';

  const [value, setValue] = useState('');
  const [messages, setMessages] = useState([]);
  const handleChange = ({ value }) => setValue(value);

  const tableMessage = `     U1210000121   <br>  .    : <ol><li></li><li></li></ol>
  <a href="/download/    S19456.xlsx" download style="color: inherit">  </a>[
      {
        "":"U1210000121",
        "":"15000000",
        "":"1000000",
        "":" ",
        " ":""
      },
      {
        "":"U1210000121",
        "":"5000000",
        "":"357048",
        "":" ",
        " ":""
      }
    ]`;
  const fileMessage =
    '     .http://146.185.234.11/download/    S19456.xlsx';

  function timestamp() {
    let time = new Date(Date.now());
    let hours = time.getHours();
    let minutes = time.getMinutes();
    return `${hours < 10 ? '0' + hours : hours}:${
      minutes < 10 ? '0' + minutes : minutes
    }`;
  }

  function setIds(tableString) {
    const tableArray = JSON.parse(tableString);
    for (let i = 0; i < tableArray.length; i++) {
      tableArray[i] = { id: i, ...tableArray[i] };
    }
    return tableArray;
  }

  function sendRequest() {
    const data = {
      user_id: userId,
      payload: value
    };
    //setMessages([...messages, { id: nextId++, message: value }]);
    setMessages((messages) => [
      ...messages,
      { id: nextId++, speaker: 'user', message: value, timestamp: timestamp() }
    ]);
    if (value.toLowerCase().indexOf('') > -1) {
      setValue(null);
      const index = fileMessage.indexOf('http://146.185.234.11');
      let text;
      let fileUrl;
      let extension;
      if (index > -1) {
        text = fileMessage.substring(0, index);
        fileUrl = fileMessage.substring(index);
        extension = fileUrl.substring(fileUrl.lastIndexOf('.') + 1);
      }
      setMessages((messages) => [
        ...messages,
        {
          id: nextId++,
          speaker: 'mion',
          message: text,
          table: null,
          fileUrl: fileUrl,
          extension: extension,
          timestamp: timestamp()
        }
      ]);
      return;
    }
    if (value.toLowerCase().indexOf('') > -1) {
      setValue(null);
      const index = tableMessage.indexOf('[');
      let text;
      let table;
      if (index > -1) {
        text = tableMessage.substring(0, index);
        table = tableMessage.substring(index);
      }
      setMessages((messages) => [
        ...messages,
        {
          id: nextId++,
          speaker: 'mion',
          message: text,
          table: setIds(table),
          fileUrl: null,
          extension: null,
          timestamp: timestamp()
        }
      ]);
      return;
    }
    // console.log(messages);
    setValue(null);
    // console.log(data.payload);
    // let url = apiUrl + value;
    axios.post(apiUrl, data).then((response) => {
      let index = response.data.response.indexOf('[');
      let text;
      let table;
      if (index > -1) {
        text = response.data.response.substring(0, index);
        table = response.data.response.substring(index);
        setMessages((messages) => [
          ...messages,
          {
            id: nextId++,
            speaker: 'mion',
            message: text,
            table: setIds(table),
            fileUrl: null,
            extension: null,
            timestamp: timestamp()
          }
        ]);
      } else if (response.data.response.indexOf('http') > -1) {
        let index = response.data.response.indexOf('http') > -1;
        let text;
        let fileUrl;
        let extension;
        if (index > -1) {
          text = fileMessage.substring(0, index);
          fileUrl = fileMessage.substring(index);
          extension = fileUrl.substring(fileUrl.lastIndexOf('.') + 1);
        }
        setMessages((messages) => [
          ...messages,
          {
            id: nextId++,
            speaker: 'mion',
            message: text,
            table: null,
            fileUrl: fileUrl,
            extension: extension,
            timestamp: timestamp()
          }
        ]);
      } else {
        setMessages((messages) => [
          ...messages,
          {
            id: nextId++,
            speaker: 'mion',
            message: response.data.response,
            table: null,
            fileUrl: null,
            extension: null,
            timestamp: timestamp()
          }
        ]);
      }
      // console.log(response.data.response);
    });
  }

  function scroll() {
    const chat = document.getElementById('chat');
    if (chat) chat.scrollTop = chat.scrollHeight;
  }

  const input = useRef(null);
  const inputContainer = useRef(null);

  function setListener() {
    input.current.id = 'input';
    document
      .getElementById('input')
      .addEventListener('keypress', function (event) {
        if (
          event.key === 'Enter' &&
          this.value !== null &&
          this.value.trim() !== ''
        ) {
          event.preventDefault();
          document.getElementById('btn').click();
        }
      });
  }

  useEffect(() => {
    setListener();
    inputContainer.current.style.background = 'var(--mion-message-color)';
  }, []);

  useEffect(() => {
    scroll();
    // setDummy();
  }, [messages]);

  function setDummy() {
    const chat = document.getElementById('chat');
    if (chat && height <= chat.scrollHeight) {
      height = chat.scrollHeight;
    }
    if (chat && height <= chat.offsetHeight * 2) {
      return <div className={styles.dummy}></div>;
    }
  }

  return (
    <div className={styles.chat} ref={innerRef}>
      <div className={styles.topBarrier}>
        <Button label="" view="clear" />
        <Button label="" view="clear" />
        <Button label="" view="clear" />
        {/* <ThemeToggler
          value="Dark"
          items={[{ label: 'Dark', icon: IconMoon }]}
        /> */}
        <Button
          view="clear"
          iconLeft={IconMoon}
          onlyIcon
          style={{ marginLeft: '4%' }}
        />
      </div>
      <div className={styles.chatScroller} id="chat">
        {setDummy()}
        {messages.map((message) => (
          <ChatRow
            key={message.id}
            speaker={message.speaker}
            text={message.message}
            timestamp={message.timestamp}
            table={message.table}
            fileUrl={message.fileUrl}
            extension={message.extension}
          />
        ))}
      </div>
      <div className={styles.inputBar}>
        <div className={styles.inputField}>
          <TextField
            onChange={handleChange}
            value={value}
            type="text"
            size="m"
            width="full"
            // className={styles.inputTextField}
            placeholder="    ..."
            form="default"
            inputRef={input}
            inputContainerRef={inputContainer}
            rightSide={IconSendMessage}
            autoFocus
          ></TextField>
        </div>
        <div className={styles.sendButton}>
          <Button
            size="l"
            iconRight={IconSendMessage}
            onlyIcon
            onClick={sendRequest}
            view="clear"
            id="btn"
          />
        </div>
      </div>
      <div className={styles.bottomBarrier}></div>
    </div>
  );
};
